import React, { useState } from 'react'
import axios from 'axios'
import userServices from '../services/login'

export const authContext = React.createContext({})

export const AuthContext = (props) => {
  const [loggedIn, setIsLoggedIn] = useState(false)
  const [loggedInUser, setLoggedInUser] = useState({ name: '', id: '' })
  const [error, setError] = useState(null)
  const [auth, setAuth] = useState(false)
  const [email, setEmail] = useState('')
  const [message, setMessage] = useState('')

  const logoutHandler = async () => {
    try {
      //   console.log('loggedInUser', loggedInUser)
      const res = await userServices.logout()
      setIsLoggedIn(false)
      setLoggedInUser({ name: '', id: '' })
      console.log('logout res.data', res)
      setMessage(res.message)
      return res
    } catch (error) {
      console.log('logout error', error)
      setError(error.message)
      setMessage('Unauthorized logout')
    }
  }

  const loginHandler = async (credentials) => {
    try {
      const response = await userServices.login(credentials)
      console.log('services.login response', response)
      setIsLoggedIn(true)
      setLoggedInUser({ name: response.name, id: response.id })
      setError('')
      setMessage('Login successful')
      return response.data
    } catch (error) {
      console.log('login error', error)
      setError(error.response.data.message)
    }
  }

  const registerHandler = async (credentials) => {
    //  console.log('register credentials', credentials)
    const registerUrl = '/users/signup'
    try {
      const response = await axios.post(registerUrl, credentials)
      if (response.data.error) {
        setError(response.data.message)
        return error
      } else {
        setError(null)
        console.log('register credentials.email', credentials.email)
        setEmail(credentials.email)
        console.log('register response.data', response.data)
        return response.data
      }
    } catch (err) {
      console.log('register axios error', err.response.data)
      setError(err.response.data.message)
    }
  }

  const authHandler = async (params) => {
    console.log('auth code params', params)

    const authUrl = '/users/activate'
    try {
      const response = await axios.patch(authUrl, params)
      if (response.data.error) {
        setError(response.data.message)
        return error
      } else {
        setAuth(false)
        setError(null)
        return response.data
      }
    } catch (err) {
      setAuth(true)
      setError(err.response.data.message)
      console.log('authorization error', err)
    }
  }

  const forgotHandler = async (email) => {
    const forgotUrl = '/users/forgot'
    try {
      const response = await axios.patch(forgotUrl, email)
      setError('')
      return response.data
    } catch (error) {
      setError(error.response.data.message)
    }
  }

  return (
    <authContext.Provider
      value={{
        isLoggedIn: loggedIn,
        onLogout: logoutHandler,
        onLogin: loginHandler,
        onRegister: registerHandler,
        onAuth: authHandler,
        auth: auth,
        user: loggedInUser,
        error: error,
        userEmail: email,
        onForgot: forgotHandler,
        message: message,
      }}
    >
      {props.children}
    </authContext.Provider>
  )
}

export default AuthContext
